:root {
  --board-rows: 7;
  --board-columns: 8;
  --tile-size: min( calc((100vw - 3rem * 2)  / var(--board-rows, 7) ), calc((100vh - 6rem * 2)  / var(--board-columns, 8) ) );
  --accent-color: #fcd53f;
  --secondary-color: #b5b3b4;
  --container-color: #475369;
  --grid-color: #273346;
  --container-padding: .5rem;
}

@font-face {
  font-family: Halloween Nightmare Demo;
  src: url("Halloween Nightmare Demo.17f3d1c0.ttf");
}

body {
  height: 100vh;
  background: url("background.e50e77c7.png") center / cover;
  margin: 0;
  font-family: Halloween Nightmare Demo, serif;
}

.content {
  width: 100%;
  box-sizing: border-box;
  padding-left: 1rem;
  padding-right: 1rem;
  padding-top: max(3rem, env(safe-area-inset-top) );
  padding-bottom: max(3rem, env(safe-area-inset-bottom) );
  height: 100%;
  flex-direction: column;
  justify-content: end;
  align-items: center;
  gap: 2rem;
  display: flex;
  position: relative;
}

.container {
  padding: var(--container-padding, .5rem);
  border: 1.5px solid #6f83a1ab;
  border-radius: 1.5rem;
  position: relative;
  overflow: hidden;
  box-shadow: 1px 1px 3px #050505;
}

.container, .statistics {
  background-color: var(--container-color, #475369);
  filter: opacity(.75) brightness(1.1) saturate(1.1) contrast(1.1);
  border: 1.5px solid #6f83a1ab;
  transition: opacity 1s ease-in-out;
}

.statistics {
  width: 100%;
  max-width: calc(var(--tile-size, 8em)  * 8);
  color: var(--accent-color);
  text-shadow: -2px 2px #19253d75;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 1rem;
  padding: 1rem;
  font-size: 2.5rem;
  position: fixed;
  top: 0;
}

.statistics > ul {
  justify-content: space-between;
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
}

.statistics > ul > li {
  text-align: center;
}

.statistics > ul > li > button {
  color: var(--accent-color);
  background: none;
  border: none;
}

.statistics.right {
  min-width: 8rem;
  width: auto;
  border-top-left-radius: 1rem;
  border-bottom-left-radius: 1rem;
  border-bottom-right-radius: 0;
  top: 50%;
  right: 0;
  transform: translateY(-50%);
}

.statistics.right > ul {
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
}

.statistics.right > ul > li {
  cursor: pointer;
}

.statistics.left {
  min-width: 8rem;
  width: auto;
  border-top-right-radius: 1rem;
  border-bottom-left-radius: 0;
  border-bottom-right-radius: 1rem;
  top: 50%;
  left: 0;
  transform: translateY(-50%);
}

.statistics.left > ul {
  flex-direction: column;
  align-items: flex-start;
  gap: 1rem;
}

.statistics.left > ul > li {
  cursor: pointer;
}

.multiplier, .game-over, .level-up, .cooking {
  z-index: 1;
  pointer-events: none;
  opacity: 0;
  color: var(--accent-color);
  text-shadow: -4px 4px 4px #3a3a3a;
  justify-content: center;
  align-items: center;
  margin: 0;
  display: flex;
  position: absolute;
  inset: 0;
}

.multiplier, .level-up {
  font-size: 10rem;
}

.multiplier {
  font-size: 16rem;
}

.game-over {
  cursor: pointer;
  background-color: #34312f82;
  font-size: 8rem;
  transition: opacity .3s cubic-bezier(.4, 0, .2, 1);
}

.cooking {
  background-color: var(--grid-color);
  background-image: url("cauldron.c752b267.png");
  background-position: 50% 35%;
  background-repeat: no-repeat;
  font-size: 2.5rem;
}

.board {
  z-index: 1;
  height: calc(var(--board-rows, 8)  * 1em);
  width: calc(var(--board-columns, 7)  * 1em);
  font-size: var(--tile-size, 8em);
  position: relative;
}

.grid {
  z-index: 0;
  top: var(--container-padding, .5rem);
  height: calc(100% - var(--container-padding, .5rem)  * 2);
  width: calc(100% - var(--container-padding, .5rem)  * 2);
  font-size: var(--tile-size, 8em);
  background-color: var(--grid-color, #273346);
  border-radius: 1rem;
  display: flex;
  position: absolute;
}

.grid .row .cell {
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  background-color: var(--container-color, #475369);
  border-left: 2px solid var(--grid-color, #273346);
  border-bottom: 2px solid var(--grid-color, #273346);
  border-radius: .5rem;
}

.grid .row .cell:first-child {
  border-top: none;
}

.grid .row .cell:last-child {
  border-bottom: none;
}

.grid .row:first-child .cell {
  border-left: none;
}

.tile {
  cursor: pointer;
  width: 1em;
  height: 1em;
  box-sizing: border-box;
  text-shadow: -2px 4px 8px #525252;
  justify-content: center;
  align-items: center;
  transition: left .3s cubic-bezier(.4, 0, .2, 1), top .3s cubic-bezier(.4, 0, .2, 1);
  display: flex;
  position: absolute;
}

.tile:before, .tile:after {
  content: "";
  width: 1em;
  height: 1em;
  background-size: cover;
}

.tile:after {
  background-image: url("data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAEAAAABCAYAAAAfFcSJAAAAAXNSR0IArs4c6QAAAAtJREFUGFdjYAACAAAFAAGq1chRAAAAAElFTkSuQmCC");
  transition: background-image .3s cubic-bezier(.4, 0, .2, 1);
  position: absolute;
}

.tile.active:after {
  background-image: url("active.8e5d0f13.png");
}

.tile.crystal:before {
  background-image: url("crystal.5dd424b0.png");
}

.tile.mushroom:before {
  background-image: url("mushroom.9beacd66.png");
}

.tile.pumpkin:before {
  background-image: url("pumpkin.d86689b2.png");
}

.tile.skull:before {
  background-image: url("skull.d695c751.png");
}

.tile.stone:before {
  background-image: url("stone.4a0d8629.png");
}

.tile.hat:before {
  background-image: url("hat.6619b2cd.png");
}

.tile.flask:before {
  background-image: url("flask.4473d58b.png");
}

.splash {
  height: 100%;
  opacity: 1;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  font-size: 1.2rem;
  transition: opacity 1s ease-in-out;
  display: flex;
  position: absolute;
  left: 50%;
  transform: translateX(-50%);
}

.splash:after {
  content: "";
  width: 100%;
  height: 100%;
  opacity: .7;
  background-image: url("sparks.b095e9ef.png");
  background-position: center;
  background-repeat: no-repeat;
  position: absolute;
}

.splash .logo {
  width: 75%;
  filter: drop-shadow(2px 4px 8px #c8c8c833);
  margin: 0 auto;
}

.splash > p {
  color: var(--secondary-color, #b5b3b4);
  letter-spacing: 1px;
  margin-bottom: .5rem;
  font-size: 1.2rem;
  font-weight: 400;
}

.loader {
  height: 25px;
  width: 552px;
  background: url("loader-container.8d3b007f.png");
  align-items: center;
  display: flex;
}

.loader .loader__bar {
  height: 15px;
  width: 400px;
  z-index: -1;
  margin-left: 4px;
  position: relative;
  overflow: hidden;
}

.loader .loader__bar:before {
  content: "";
  width: 100%;
  height: 100%;
  background: url("loader-bar.97094066.png");
  margin-left: -100%;
  animation: loader-bar-slide 5s ease-in-out infinite;
  display: block;
  position: absolute;
}

@keyframes loader-bar-slide {
  100% {
    margin-left: 0;
  }
}

